 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards;animation:fadeOut 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards}.hamburger .main-cursor-background{-webkit-animation:scaleUp 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards;animation:scaleUp 1s cubic-bezier(0.77, 0, 0.175, 1) 0s forwards}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@-webkit-keyframes fadeOut{0%{opacity:1}100%{opacity:0}}@keyframes fadeOut{0%{opacity:1}100%{opacity:0}}@-webkit-keyframes bgUp{from{background-color:transparent}to{background-color:#000}}@keyframes bgUp{from{background-color:transparent}to{background-color:#000}}@-webkit-keyframes bgDown{from{background-color:#000}to{background-color:transparent}}@keyframes bgDown{from{background-color:#000}to{background-color:transparent}}@-webkit-keyframes scaleUp{from{-webkit-transform:scale(1);transform:scale(1)}to{-webkit-transform:scale(1.5);transform:scale(1.5)}}@keyframes scaleUp{from{-webkit-transform:scale(1);transform:scale(1)}to{-webkit-transform:scale(1.5);transform:scale(1.5)}}@-webkit-keyframes scaleDown{from{-webkit-transform:scale(1.5);transform:scale(1.5)}to{-webkit-transform:scale(1);transform:scale(1)}}@keyframes scaleDown{from{-webkit-transform:scale(1.5);transform:scale(1.5)}to{-webkit-transform:scale(1);transform:scale(1)}}@-webkit-keyframes translateLeftDot{from{-webkit-transform:translate(20px, -50%);transform:translate(20px, -50%)}to{-webkit-transform:translate(0px, -50%);transform:translate(0px, -50%)}}@keyframes translateLeftDot{from{-webkit-transform:translate(20px, -50%);transform:translate(20px, -50%)}to{-webkit-transform:translate(0px, -50%);transform:translate(0px, -50%)}}@-webkit-keyframes translateRightDot{from{-webkit-transform:translate(-20px, -50%);transform:translate(-20px, -50%)}to{-webkit-transform:translate(0px, -50%);transform:translate(0px, -50%)}}@keyframes translateRightDot{from{-webkit-transform:translate(-20px, -50%);transform:translate(-20px, -50%)}to{-